<template>
  <div>
    <Stats :stat_info="statInfo" @myEvent="tableStats" />
    <CRow>
      <CCol md="12">
        <CCard class="clapans">
          <CCardHeader>
            <div class="d-flex aligin-items-center justify-content-between">
              <span>Мастеры</span>
              <div>
                <CButton color="success" class="mr-2" @click="downloadArray">
                  Скачать историю Мастера в csv
                </CButton>
              </div>
            </div>
          </CCardHeader>
          <CCardBody>
            <div class="d-flex aligin-items-center justify-content-between">
              <CSelect
                v-if="isSelect"
                horizontal
                label="Действие на выбор"
                :value.sync="action_switcher"
                :options="[
                  'Не выбрано ничего',
                  'Включить отмеченные',
                  'Отключить отмеченные',
                ]"
                v-on:change="actionSwitcher()"
                class="col-4"
              />
              <CSelect
                horizontal
                label="Город"
                :options="cities_name"
                :value.sync="action_switcher_city"
                v-on:change="actionSwitcherCity()"
                class="col-4"
              />
            </div>
            <CDataTable
              class="mb-0 table-outline"
              hover
              :items="tableItems"
              :fields="tableFields"
              head-color="light"
              column-filter
              table-filter
              sorter
              :tableFilter="{
                label: 'Поиск',
                placeholder: 'Введите слово для поиска',
              }"
            >
              <template #select-filter="{ item }">
                <CInputCheckbox
                  :value.sync="all_select"
                  custom
                  inline
                  @change="allSelect()"
                />
              </template>
              <td slot="select" slot-scope="{ item, index }">
                <input
                  type="checkbox"
                  v-model="item.select"
                  custom
                  inline
                  @change="actionSelectChange(index)"
                />
              </td>
              <td slot="status" slot-scope="{ item, status }">
                <span
                  class="badge"
                  :class="'badge-' + colorStatus(item.status)"
                  >{{ textStatus(item.status) }}</span
                >
                <br />
                <CSwitch
                  class="mt-1"
                  color="primary"
                  :checked="item.status === '4' || item.status === '5'"
                  :value="item.status"
                  @update:checked="actionStatusChange(item)"
                />
              </td>
              <td slot="city" slot-scope="{ item }">
                {{ cities_key[item.city] }}
              </td>
              <td slot="district" slot-scope="{ item }">
                {{ item.district }}
              </td>
              <td slot="street" slot-scope="{ item }">
                {{ item.street }}
              </td>
              <td slot="building" slot-scope="{ item }">
                {{ item.building }}
              </td>
              <td slot="numHouse" slot-scope="{ item }">
                {{ item.typeofbuilding }}
              </td>
              <td slot="numDoor" slot-scope="{ item }">
                {{ item.buildingnum }}
              </td>
              <td slot="numMine" slot-scope="{ item }">
                {{ item.entrancenum }}
              </td>
              <td slot="stage" slot-scope="{ item }">
                {{ item.shaftnum }}
              </td>
              <td slot="numOffice" slot-scope="{ item }">
                {{ item.floor }}
              </td>
              <td slot="id" slot-scope="{ item }">
                {{ item.masterID }}
              </td>

              <td slot="settings" slot-scope="{ item, index }">
                <span
                  style="white-space: nowrap; cursor: pointer"
                  class="d-flex align-items-center"
                  @click="showSettingModal(item)"
                  ><CIcon name="cilSettings" class="mr-1" /> Настройки
                </span>
                <span
                  style="white-space: nowrap; cursor: pointer"
                  class="d-flex align-items-center"
                  @click="$router.push({ path: '/master/' + item.masterID })"
                  ><CIcon name="cilSettings" class="mr-1" /> История
                </span>
              </td>
            </CDataTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
    <CModal title="Добавить нового Мастера" :show.sync="NewMasterModal">
      <CCard>
        <CCardBody>
          <CForm v-on:submit="submitForm()">
            <CSelect
              horizontal
              label="Город"
              :value.sync="setting.city"
              :options="cities"
              v-model="setting.city"
              required
            />
            <!-- <CInput
              type="text"
              label="Свой вариант"
              v-model="setting.city"
              horizontal
            /> -->
            <!-- <CSelect
              horizontal
              label="Улица"
              :value.sync="setting.street"
              :options="streets"
            /> -->
            <CInput
              type="text"
              v-model="setting.street"
              label="Улица"
              horizontal
              required
            />
            <!-- <CSelect
              horizontal
              label="Подъезд"
              :value.sync="setting.entrancenum"
              :options="entrances"
            /> -->
            <CInput
              type="text"
              v-model="setting.entrancenum"
              label="Подъезд"
              horizontal
            />
            <!-- <CSelect
              horizontal
              label="Шахта"
              :value.sync="setting.shaftnum"
              :options="shaftnums"
            /> -->
            <CInput
              type="text"
              v-model="setting.shaftnum"
              label="Шахта"
              horizontal
              required
            />
            <!-- <CSelect
              horizontal
              label="Этаж"
              :value.sync="setting.floor"
              :options="floors"
            /> -->
            <CInput
              type="text"
              v-model="setting.floor"
              label="Этаж"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.district"
              label="Район"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.typeofbuilding"
              label="Тип здания"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.buildingname"
              label="Название здания"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.buildingnum"
              label="Номер дома"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.masterhubID"
              label="ID Мастера"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.path"
              label="Путь Мастера"
              horizontal
              required
            />
            <CButton color="success" type="submit"> Добавить </CButton>
          </CForm>
        </CCardBody>
      </CCard>
      <div slot="footer"></div>
    </CModal>
    <CModal title="Настройки Мастера" :show.sync="EditModal">
      <CCard>
        <CCardBody>
          <CForm v-on:submit="editForm()">
            <CSelect
              horizontal
              label="Город"
              :value.sync="setting.city"
              :options="cities"
              required
            />
            <CInput
              type="text"
              v-model="setting.city"
              label="Свой вариант"
              horizontal
              required
            />
            <CSelect
              horizontal
              label="Улица"
              :value.sync="setting.street"
              :options="streets"
              required
            />
            <CInput
              type="text"
              v-model="setting.street"
              label="Свой вариант"
              horizontal
              required
            />
            <CSelect
              horizontal
              label="Подъезд"
              :value.sync="setting.entrancenum"
              :options="entrances"
              required
            />
            <CInput
              type="text"
              v-model="setting.entrancenum"
              label="Свой вариант"
              horizontal
              required
            />
            <CSelect
              horizontal
              label="Шахта"
              :value.sync="setting.shaftnum"
              :options="shaftnums"
              required
            />
            <CInput
              type="text"
              v-model="setting.shaftnum"
              label="Свой вариант"
              horizontal
              required
            />
            <CSelect
              horizontal
              label="Этаж"
              :value.sync="setting.floor"
              :options="floors"
              required
            />
            <CInput
              type="text"
              v-model="setting.floor"
              label="Свой вариант"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.district"
              label="Район"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.typeofbuilding"
              label="Тип здания"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.buildingname"
              label="Название здания"
              horizontal
              required
            />
            <CInput
              type="text"
              description="buildingnum "
              v-model="setting.buildingnum"
              label="Номер дома"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.masterhubID"
              label="ID Мастера"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.path"
              label="Путь Мастера"
              horizontal
              required
            />
            <CButton color="success" type="submit"> Изменить </CButton>
          </CForm>
        </CCardBody>
      </CCard>
      <div slot="footer"></div>
    </CModal>
    <div class="alert_block">
      <CAlert color="warning" closeButton v-if="warning">
        {{ alert_message }}
      </CAlert>
      <CAlert color="primary" closeButton v-if="success">
        {{ alert_message }}
      </CAlert>
    </div>
  </div>
</template>

<script>
import Stats from "./widgets/Stats";
import downloadCsv from "download-csv";

const qs = require("qs");
const axios = require("axios");
export default {
  name: "Master",
  components: {
    Stats,
  },
  data() {
    return {
      statInfo: [],
      cities_name: "",
      cities_arr: "",
      all_select: false,
      warning: false,
      success: false,
      alert_message: "",
      NewMasterModal: false,
      EditModal: false,
      isSelect: false,
      action_switcher: "",
      action_switcher_city: "",
      id: "",
      tableItems: [],
      tableFields: [
        { key: "select", label: "Выбор", sorter: false, filter: false },
        { key: "status", label: "Статус" },
        { key: "city", label: "Город " },
        { key: "district", label: "Район ", _classes: "text-center" },
        { key: "street", label: "Улица " },
        { key: "typeofbuilding", label: "Вид здания" },
        { key: "buildingnum", label: "Номер дома " },
        { key: "entrancenum", label: "Номер подъезда " },
        { key: "shaftnum", label: "Номер шахты " },
        { key: "floor", label: "Этаж" },
        { key: "settings", label: "Действия" },
      ],
      setting: {},
      cities: [
        { value: null, label: "Выберите город" },
        { value: "1", label: "Алма-ата" },
        { value: "2", label: "Нур-Султан" },
        { value: "3", label: "Шымкент" },
        { value: "4", label: "Актобе" },
        { value: "5", label: "Караганда" },
        { value: "6", label: "Тараз" },
        { value: "7", label: "Павлодар" },
        { value: "8", label: "Усть-Каменогорск" },
        { value: "9", label: "Семей" },
        { value: "10", label: "Атырау" },
        { value: "11", label: "Костанай" },
        { value: "12", label: "Кызылорда" },
        { value: "13", label: "Уральск" },
        { value: "14", label: "Петропавловск" },
        { value: "15", label: "Темиртау" },
        { value: "16", label: "Актау" },
        { value: "17", label: "Туркестан" },
        { value: "18", label: "Кокшетау" },
        { value: "19", label: "Талдыкорган" },
        { value: "20", label: "Экибастуз" },
        { value: "21", label: "Рудный" },
      ],
      districts: [
        { value: "Сарыаркинский", label: "Сарыаркинский" },
        { value: "Есиль", label: "Есиль" },
      ],
      streets: [
        { value: "Сарыаркинский", label: "Сарыаркинский" },
        { value: "Есиль", label: "Есиль" },
      ],
      buildings: [
        { value: "Сарыаркинский", label: "Сарыаркинский" },
        { value: "Есиль", label: "Есиль" },
      ],
      houses: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      entrances: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      aptoroffices: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      shaftnums: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      floors: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      types: [
        { value: 1, label: "Физ лицо" },
        { value: 2, label: "Юр лицо" },
      ],
      payments: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
    };
  },

  mounted() {
    let city = auth.cities();
    this.cities_name = city.cities_name;
    this.cities_arr = city.cities;
    this.cities_key = city.cities_key;
    this.getResults(1);
    this.interval = setInterval(() => this.getResults(1), 30000);
  },
  methods: {
      tableStats(val){
      if (val === 1){
        this.tableItems = this.statInfo[1]
      }
      else if (val === 2){
        this.tableItems = this.statInfo[2]
      }
      else {
        this.tableItems = this.statInfo[4]
      }
    },
    showSettingModal(item) {
      this.setting = item;
      this.EditModal = !this.EditModal;
    },
    addNewMasterModal() {
      this.setting = {};
      this.NewMasterModal = !this.NewMasterModal;
    },
    downloadArray() {
      const datas = this.tableItems;
      const columns = {
        select: "Выбор",
        status: "Статус клапана",
        city: "Город ",
        district: "Район ",
        street: "Улица ",
        typeofbuilding: "Вид здания",
        buildingnum: "Номер дома ",
        entrancenum: "Номер подъезда ",
        shaftnum: "Номер шахты ",
        floor: "Этаж",
        aptoroffice: "Кв/офис",
        valveID: "ID клапана",
        settings: "Действия",
      };
      const current = new Date();
      const date = `${current.getDate()}-${
        current.getMonth() + 1
      }-${current.getFullYear()}`;
      let name = "";
      if (this.action_switcher_city.length >= 1) {
        name =
          "Мастеры в городе" + this.action_switcher_city + " " + date + ".csv";
      } else {
        name = "Мастеры в городе" + " Алма-Ата " + date + ".csv";
      }
      downloadCsv(datas, columns, name);
    },
    allSelect() {
      let app = this;
      app.isSelect = true;
      for (var i = 0; i < app.tableItems.length; i++) {
        if (app.all_select == false) {
          this.tableItems[i].select = true;
          this.tableItems[i].street = this.tableItems[i].street + " ";
        } else {
          this.tableItems[i].select = false;
          this.tableItems[i].street = this.tableItems[i].street + " ";
        }
      }
      app.all_select = app.all_select == true ? false : true;
    },
    submitForm() {
      event.preventDefault();
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/addmaster/",
        data: qs.stringify(this.setting),
      })
        .then(({ data }) => {
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно добавлено";
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
          this.getResults(1);
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    editForm() {
      event.preventDefault();
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/updatemaster/",
        data: qs.stringify(this.setting),
      })
        .then(({ data }) => {
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно изменено";
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
          this.getResults(1);
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    actionSelectChange(value) {
      let app = this;
      app.isSelect = true;
      if (app.tableItems[value].select == false) {
        app.tableItems[value].select = true;
        app.tableItems[value].street = this.tableItems[value].street + " ";
      } else {
        app.tableItems[value].select = false;
        app.tableItems[value].street = this.tableItems[value].street + " ";
      }
    },
    actionSwitcher() {
      let app = this;
      for (var i = 0; i < app.tableItems.length; i++) {
        if (app.tableItems[i].select == true) {
          if (this.action_switcher == "Включить отмеченные") {
            axios.defaults.headers.common["Authorization"] =
              "Bearer " + window.auth.token;
            axios({
              method: "post",
              url: "/turnon/",
              data: qs.stringify({
                device_id: app.tableItems[i].masterID,
              }),
            })
              .then(({ data }) => {
                app.success = true;
                app.alert_message = data.responce;
                setTimeout(function () {
                  app.success = false;
                }, 3000);
              })
              .catch(function (error) {});
          } else if (this.action_switcher == "Отключить отмеченные") {
            axios.defaults.headers.common["Authorization"] =
              "Bearer " + window.auth.token;
            axios({
              method: "post",
              url: "/turnoff/",
              data: qs.stringify({
                device_id: app.tableItems[i].masterID,
              }),
            })
              .then(({ data }) => {
                app.success = true;
                app.alert_message = data.responce;
                setTimeout(function () {
                  app.success = false;
                }, 3000);
              })
              .catch(function (error) {});
          } else {
          }
        }
      }
    },
    actionSwitcherCity() {
      this.getResults(this.cities_arr[this.action_switcher_city]);
    },
    actionStatusChange(item) {
      console.log(this.action_switcher_city);
      let app = this;
      var status =
        item.status == 1 || item.status == 2 || item.status == 0
          ? "/turnonmaster/"
          : "/turnoffmaster/";
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: status,
        data: qs.stringify({
          device_id: item.masterID,
        }),
      })
        .then(({ data }) => {
          app.success = true;
          app.alert_message = data.responce;
          setTimeout(function () {
            app.success = false;
          }, 3000);

          if (this.cities_arr[this.action_switcher_city] == undefined) {
            this.getResults(1);
          } else {
            this.getResults(this.cities_arr[this.action_switcher_city]);
          }
        })
        .catch(function (error) {});
    },
    colorStatus(value) {
      let color = "secondary";
      if (value == 1) {
        color = "secondary";
      } else if (value == 2 || value == 4) {
        color = "warning";
      } else if (value == 3) {
        color = "danger";
      } else if (value == 5) {
        color = "success";
      }
      return color;
    },
    textStatus(value) {
      var color = "Выключен";
      if (value == 1) {
        color = "Отключен";
      } else if (value == 2) {
        color = "Отключается";
      } else if (value == 3) {
        color = "Сломался";
      } else if (value == 4) {
        color = "Включается";
      } else if (value == 5) {
        color = "Включен";
      } else {
        color = "Выключен";
      }
      return color;
    },
    color(value) {
      let $color;
      if (value <= 25) {
        $color = "info";
      } else if (value > 25 && value <= 50) {
        $color = "success";
      } else if (value > 50 && value <= 75) {
        $color = "warning";
      } else if (value > 75 && value <= 100) {
        $color = "danger";
      }
      return $color;
    },
    getResults(city) {
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios
        .get("/getmasterbycity/" + city)
        .then((response) => {
          app.tableItems = response.data[0];
          this.statInfo = response.data;
          for (var i = 0; i < app.tableItems.length; i++) {
            app.tableItems[i].select = false;
          }
        })
        .catch((error) => {
          if (error.response.status == 401) {
            window.auth.logout();
          }
        });
    },
  },
};
</script>
<style>
.clapans .form-inline {
  margin-left: 15px;
}

.alert_block {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 100000;
}
</style>
